.cardsC { background: url(../../images/bg-cards.gif);

    .cards { @include gridContainer; padding-top: 35px; padding-bottom: 15px;
        
        a { position: relative; @include gallery(4 of 12); margin-bottom: gutter(); display: block; box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.10);
            
            .cardBtnIndicator { position: absolute; bottom: 10px; right: 10px; z-index: 11;
                
                img { width: 23px; }
                
            }//cardBtnIndicator
            
            .trendIndicator { position: absolute; bottom: 180px; left: 0px; width: 100%; height: 43px; padding: 13px 33px; background-color: rgba(0, 37, 98, 0.95); z-index: 10; color: #FFF;
                
                &::before { content: url(../../images/trending-icon.png); margin-right: 10px; }
                
                //*** Breakpoints ***
                @include breakpoint($bp2) { bottom: 160px; }
                
            }//trendIndicator
            
            .overlayC { position: absolute; bottom: 0px; left: 0px; width: 100%; height: 180px; padding: 15px 33px 0px 15px; background-color: rgba(255, 255, 255, 0.95); z-index: 10; 
                
                .overlay { width: 100%; height: 128px; overflow: hidden;
                    
                    .cardTitle { color: #0c419a; }
                    .cardHdr { padding-top: 10px; color: #0c419a; font: 20px/24px HelveticaNeueLT-Thin;
                        
                        //*** Breakpoints ***
                        @include breakpoint($bp2) { padding-top: 5px; font-size: 16px; line-height: 20px; } 
                            
                    }//cardHdr
                    .cardTxt { padding-top: 10px; color: #595959; font-size: 12px; line-height: 16px;
                        
                        //*** Breakpoints ***
                        @include breakpoint($bp2) { padding-top: 5px; font-size: 11px; line-height: 13px; } 
                        
                    }//cardTxt            
                   
                    //*** Breakpoints ***
                    @include breakpoint($bp2) { height: 107px; } 
                    
                }//overlay                
                                    
                .cardDate { padding-top: 5px; color: #595959; font-size: 11px; }
                
                //*** Breakpoints ***
                @include breakpoint($bp2) { padding: 15px 15px 0px 15px; height: 160px; }
                
            }//overlayC
            
            img { display: block; width: 100%; }
          
            //*** Breakpoints ***
            @include breakpoint($bp1) { @include gallery(6 of 12 break); }
            @include breakpoint($bp3) { 
                @include gallery(12 of 12 break); 
                &:nth-child(n) {
                    clear: both;   
                } 
            }//breapoint 3
            
        }//a
        
        a.trending .overlayC { background: #124397!important; }
        a.trending .cardTitle, a.trending .cardHdr, a.trending .cardTxt, a.trending .cardDate { color: #FFF!important; }
        
    }//cards
    
}//cardsC